@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap');

* {
  box-sizing: border-box;
}

body {
  background-color: #22254b;
  color: white;
  font-family: 'Montserrat', sans-serif;
  margin: 0;
}

/* .root {
  display: flex;
  flex-direction: column;
} */

header {
  background-color: #373b69;
  padding: 1rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.search {
  background-color: transparent;
  border: 2px solid #22254b;
  border-radius: 50px;
  padding: 0.5rem 1.5rem;
  font-size: 1.2rem;
  font-family: inherit;
  color: white;
}

.search:focus {
  outline: none;
}

.movie-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.movie {
  width: 250px;
  margin: 1rem;
  background-color: #373b69;
  border-radius: 3px;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  position: relative;
}

.movie img {
  height: 400px;
  object-fit: cover;
  width: 100%;
}

.movie-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
}

.movie-info h3 {
  margin: 0;
}

.movie-overview {
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  box-shadow: 0 -3px 5px rgba(0, 0, 0, 0.1);
  background-color: #fff;
  bottom: 0;
  left: 0;
  right: 0;
  color: #22254b;
  position: absolute;
  padding: 1rem;
  transform: translateY(102%);
  transition: 0.3s;
  overflow: auto;
  max-height: 100%;
}

.movie:hover .movie-overview {
  transform: translateY(0%);
}

.tag {
  background-color: #22254b;
  padding: 0.5rem;
  border-radius: 5px;
  font-weight: bold;
}

.tag.green {
  color: rgb(20, 177, 20);
}

.tag.orange {
  color: orange;
}

.tag.red {
  color: red;
}

.band {
  color: white;
  text-decoration: none;
}
